import {LoginLayout,DefaultLayout} from './layouts';
import {Login,Dashboard,Role,Admin} from './pages';
import Utils from './Utils';

const CustomRoutes = [  
  
  {
    'path'      : '/login', 
    'secure'    : false,    
    'exact'     : true,
    'layout'    : LoginLayout, 
    'component' : Login
  },
  {
    'path'      : '/roles',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Role,
    'accessible': Utils.canAccess('roles')
  },
  {
    'path'      : '/admin',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Admin,
    'accessible': Utils.canAccess('admin')
  },
  {
    'path'      : '/',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Dashboard
  },
  {
    'path'      : '*',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Dashboard
  }
];

export default CustomRoutes;