import React, { useContext } from 'react'

import UserContext from '../contexts/UserContext'

const Header = (props) => {

  const {logout} = useContext(UserContext);

  const logAdminOut = (e) => {
    e.preventDefault();
    logout(() => {
      window.location.href = "/login";
    });  
  }
  
  return (<div>
    <nav className="main-header navbar navbar-expand navbar-white navbar-light">
      {/* Left navbar links */}
      <ul className="navbar-nav">
        <li className="nav-item">
          <a className="nav-link" data-widget="pushmenu" href="#"><i className="fas fa-bars" /></a>
        </li>
        <li className="nav-item d-none d-sm-inline-block">
          <a href="index3.html" className="nav-link">Home</a>
        </li>
        <li className="nav-item d-none d-sm-inline-block">
          <a href="#" className="nav-link">Contact</a>
        </li>
      </ul>
      {/* SEARCH FORM */}
      <form className="form-inline ml-3">
        <div className="input-group input-group-sm">
          <input className="form-control form-control-navbar" type="search" placeholder="Search" aria-label="Search" />
          <div className="input-group-append">
            <button className="btn btn-navbar" type="submit">
              <i className="fas fa-search" />
            </button>
          </div>
        </div>
      </form>
      {/* Right navbar links */}
      <ul className="navbar-nav ml-auto">
      <li className="nav-item dropdown user-menu">
        <a href="#" className="nav-link dropdown-toggle" data-toggle="dropdown">
          <img src="/dist/img//user2-160x160.jpg" className="user-image img-circle elevation-2" alt="User Image" />
          <span className="d-none d-md-inline">Alexander Pierce</span>
        </a>
        <ul className="dropdown-menu dropdown-menu-lg dropdown-menu-right">
          <li className="user-header bg-primary">
            <img src="/dist/img/user2-160x160.jpg" className="img-circle elevation-2" alt="User Image" />

            <p>
              Alexander Pierce - Web Developer
              <small>Member since Nov. 2012</small>
            </p>
          </li>
          <li className="user-footer">
            <a href="#" className="btn btn-default btn-flat">Profile</a>
            <a className="btn btn-default btn-flat float-right" onClick={logAdminOut}>Sign out</a>
          </li>
        </ul>
      </li>
        {/* Messages Dropdown Menu */}
        <li className="nav-item">
          <a className="nav-link" data-widget="control-sidebar" data-slide="true" onClick={logAdminOut}>
            <i className="fas fa-sign-out-alt" />
          </a>
        </li>
      </ul>
    </nav>
  </div>)
};

export default Header;
